import { Component, OnInit, OnChanges, DoCheck, AfterContentChecked, AfterContentInit, OnDestroy } from '@angular/core';
import { Joke } from '../beans/joke';

@Component({
  selector: 'app-lifecycle-hooks',
  templateUrl: './lifecycle-hooks.component.html',
  styleUrls: ['./lifecycle-hooks.component.scss']
})
export class LifecycleHooksComponent implements OnInit,OnChanges,DoCheck,AfterContentChecked,AfterContentInit,OnDestroy {

  jokes: Joke[] = [];

  addJoke() {
    this.jokes.unshift(new Joke("What did the cheese say when it looked in the mirror", "Hello-me (Halloumi)"));
  }

  deleteJoke() {
    this.jokes = []
  }
  
  constructor() { }

 

  ngOnChanges() {
    console.log(`ngOnChanges - data is ${this}`);
  }

  ngOnInit() {
    console.log(`ngOnInit  - data is ${this}`);
  }

  ngDoCheck() {
    console.log("ngDoCheck")
  }

  ngAfterContentInit() {
    console.log("ngAfterContentInit");
  }

  ngAfterContentChecked() {
    console.log("ngAfterContentChecked");
  }

  ngAfterViewInit() {
    console.log("ngAfterViewInit");
  }

  ngAfterViewChecked() {
    console.log("ngAfterViewChecked");
  }

  ngOnDestroy() {
    console.log("ngOnDestroy");
  }
}
