import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import {Routes,RouterModule} from '@angular/router';
import {HeroesComponent } from './heroes/heroes.component'
import {DashboardComponent} from './dashboard/dashboard.component';
import {BookComponent} from './book/book.component';
import {AboutusComponent} from './aboutus/aboutus.component';
import {UserformModule} from './userform/userform. module';
import {PageNotFoundComponent} from './page-not-found/page-not-found.component';
import {TableComponent} from './table/table.component';
import { ChartsComponent } from './charts/charts.component';
import {RecaptchaComponent} from './recaptcha/recaptcha.component';
import { GlobalErrorComponent } from './global-error.component';
import { PreloadModulesStrategy } from './core/strategy/preload.strategy.module';
const routes:Routes=[
  { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
  {path:'dashboard',component:DashboardComponent,pathMatch:'full'},
  {path:'hero',component: HeroesComponent},
  {path:'book',component:BookComponent},
  {path:'aboutus',component:AboutusComponent},
  {path:'userform',redirectTo:'/userform' ,pathMatch:'full'},
  {path: 'country',redirectTo: '/country', pathMatch: 'full'} ,
  {path: 'person',redirectTo: '/person', pathMatch: 'full'}  ,
  //{path:'login',redirectTo:'/login',pathMatch:'full'},
  {path:'table',component:TableComponent},
  {path:'chart',component:ChartsComponent},
  {path:'recaptcha',component:RecaptchaComponent},
  {path: 'fileUpload',redirectTo: '/fileUpload', pathMatch: 'full'}  ,
  {path: 'youtube',loadChildren: () => import('./youtube/youtube.module').then(mod => mod.YoutubeModule)},
  
  {path: 'oauth2',redirectTo: '/oauth2', pathMatch: 'full'},
  {path: 'misc',redirectTo: '/misc', pathMatch: 'full'},
  {path: 'userTemplate',loadChildren: () => import('./user-template/user-template.module').then(mod => mod.UserTemplateModule)},
  {path:'login1', loadChildren:()=>import('./login1/login1.module').then(mod=>mod.Login1Module)},
  {
    path: 'login-basic',
    loadChildren:()=>import('./authentication/authentication.module').then(mod => mod.AuthenticationModule)
 //      loadChildren: './authentication/auth.module#AuthModule'
 }
 // {path: 'error',component: GlobalErrorComponent},
//  {path: '**',component: PageNotFoundComponent }	
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  //,{ enableTracing: true } => From Tracing purpose
  exports: [RouterModule]
})
export class AppRoutingModule { }
