import { Component, OnInit } from '@angular/core';
import { Observable, of, concat, throwError } from 'rxjs';
import { switchMap, map, merge, retry, catchError } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { Book } from './book';
import { BookService } from './book.service';
@Component({
  selector: 'app-book',
  templateUrl: './book.component.html',
  styleUrls: ['./book.component.css']
})
export class BookComponent implements OnInit {

  allBooks$: Observable<Book[]>;
  favBook$: Observable<Book>;
  myAllFavBooks$: Observable<Book[]>;
  favBookName$: Observable<Book>;
  similarBook$: Observable<Book[]>;
  softBooks: Book[];
  allFavBooks: Book[];
  bookName: string | {};
  similarBooks: Book[];
  book:Book;



  constructor(private bookService: BookService) { }

  ngOnInit() {
    this.getBooks();
    this.getFavBook();
    this.getSoftBooks();
    this.getBookName();
   
  }

  getBooks() {
    this.allBooks$ = this.bookService.getBooksFromStore();
  }

  getFavBook() {
    this.favBook$ = this.bookService.getFavBookFromStore(3);
  }

  getSoftBooks() {
    this.bookService.getBooksFromStore().subscribe(books => {
      this.softBooks = books;
    }, (err: HttpErrorResponse) => {
      if (err.error instanceof Error) {
        console.log('An error occurre', err.error.message);
      } else {
        console.log('Backend error', err.status);
        console.log('Response Body', err.error);
      }

    }

    );
  }

  getAllFavBooks() {
    return merge(this.bookService.getFavBookFromStore(2)
      , (book => this.bookService.getBooksByCategoryFromStore(book.category)));

    // Using subscribe

  }
  getBookName() {
   // this.favBookName$ = this.bookService.getFavBookFromStore(101).pipe(map(book => book.name));

    // Using subscribe
     this.bookService.getFavBookFromStore(1)
      .pipe(map(book => {
        if (book.name.length < 15) {
          return book.name;
        } else {
          throw ('Length less than 15');
        }
      }))
      .subscribe(name => {
        this.bookName = name;
        console.log(name);
      },
      err => {
        console.log(err);
      }
      );
  }
  searchSimilarBooks(id: number) {

    this.similarBook$ = this.bookService.getFavBookFromStore(id)
      .pipe(switchMap(book => {
        let category = book.category;
        return this.bookService.getBooksByCategoryFromStore(category);
      }));

    // Using subscribe
    this.bookService.getFavBookFromStore(id)
      .pipe(switchMap(book => {
        let category = book.category;
        return this.bookService.getBooksByCategoryFromStore(category);
      }))
      .subscribe(books => {
        this.similarBooks = books;
        console.log(books);
      });
  }
} 
