import { Component, Input } from "@angular/core";
import { Hero } from './hero';

@Component({
    selector:'app-child',
    template:`
    <div class='container'>
     <h3>{{hero.name}}</h3>
     <p>I , {{hero.name}} is available for your service {{_masterName}}. </p>
    </div>
    `
})
export class Child2Component{

    @Input()hero : Hero;
    
    private _masterName: string;

    public get masterName(): string {
        return this._masterName;
    }
    
    @Input('master')
    public set masterName(value: string) {
        this._masterName = (value && value.trim())|| 'value not set from parent';
    }



}