import { Component } from "@angular/core";

@Component({
    selector:'parent-timer',
    template:`
        <h2>Count down timer:Parent interacts with child via local variable</h2>
        <h3>Count down to lift off</h3>
        <button (click)="timer.start()">Start</button>
        <button (click)="timer.stop()">Stop</button>

        <div class="seconds">{{timer.seconds}}</div>
        <child-timer #timer></child-timer>

        <p>The local variable approach is simple and easy. But it is limited because the parent-child wiring must be done entirely within the parent template. The parent component itself has no access to the child.

        You can't use the local variable technique if an instance of the parent component class must read or write child component values or must call child component methods.
        
        When the parent component class requires that kind of access, inject the child component into the parent as a ViewChild.</p>
    `,
    styleUrls:['./input-output.component.scss']
})
export class CountdownTimerParentComponent{
    
}
