import { Component, ViewChild, AfterViewInit } from "@angular/core";
import { CountdownTimerComponent } from './CountdownTimerComponent';

@Component({
    selector:'timer-parent-vc',
    template:`
    <h2>Count down timer using @ViewChild</h2>
    <button (click)="start()">Start</button>
    <button (click)="stop()">Stop</button>
    <div class="seconds">{{seconds()}}</div>
    <child-timer></child-timer>
    `,
    styleUrls:['./input-output.component.scss']

})
export class CountdownTimeParentComponentVC implements AfterViewInit{
    
    @ViewChild(CountdownTimerComponent,{static:false})
    private countdownTimerComponent: CountdownTimerComponent;

    seconds(){
        return 0;
    }
    ngAfterViewInit(): void {
        setTimeout(
            ()=> this.seconds=()=>this.countdownTimerComponent.seconds,0
        );
    }

    start(){
        this.countdownTimerComponent.start();
    }

    stop(){
        this.countdownTimerComponent.stop();
    }

}