import { Component } from "@angular/core";


@Component({
    template: `<h2>Two types forms:</h2>
      <nav [ngClass] = "'child-menu'">
        <ul>
          <li><a [routerLink]="['template']" routerLinkActive="active">Template Driven Form</a></li>
          <li><a [routerLink]="['reactive']" routerLinkActive="active">Reactive Forms</a></li>
        </ul>  
      </nav>  
      <div [ngClass] = "'child-container'">	
        <router-outlet></router-outlet>	
      </div>
    `
  })
export class UserFormComponent{}