import { Component, OnInit } from '@angular/core';
import {Chart} from 'chart.js';
@Component({
  selector: 'app-aboutus',
  templateUrl: './aboutus.component.html',
  styleUrls: ['./aboutus.component.css']
})
export class AboutusComponent implements OnInit {

  LineChart=[];
  constructor() { }

  ngOnInit() {
    this.LineChart = new Chart('lineChart',this.getChartData('line') );
  }

  getChartData(type:string){
    var chartTemp = {
      type: type,
      data: {
          labels: ["Capgemini", "BNY", "ADP"],
          datasets: [{
              label: 'Years Of Experience',
              data: [3,4,6,8],
              backgroundColor: [
                  'rgba(97, 171, 64, 0.2)',
                  
              ],
              borderColor: [
                  'rgba(0, 178, 117,1)',
                  
              ],
              borderWidth: 1
          }]
      },
      options: {
          scales: {
              yAxes: [{
                  ticks: {
                      beginAtZero:true
                  }
              }]
          }
      }
  }
    return  chartTemp ;
  }

}
