import { Component } from "@angular/core";
import { HEROES } from './hero';

@Component({
    selector:'app-parent',
    template:`
    <h2>Intercept input property changes with a setter</h2>
    <h2>{{master}} control , {{heroes.length}} heroes</h2>
    <app-child *ngFor="let hero of heroes" [hero]="hero" [master]="master"></app-child>
    `

})
export class Parent2Component{
    heroes=HEROES;
    master='Master';
}