import { Component } from "@angular/core";

@Component({
    selector:'version-parent',
    template:`
    <h2>Intercept input property changes with ngOnChanges()</h2>
    <h2>Source code version:</h2>
    <button (click)="Minor()" >New Minor Version</button>
    <button (click)="Major()" >New Major Version</button>

    <version-child [major]="major" [minor]="minor" ></version-child>
    `
})
export class VersionParentComponent{

    minor:number=1;
    major:number=20;

    Minor(){
        this.minor++;
    }

    Major(){
        this.major++;
        this.minor=0;
    }
}