import { Component, OnInit } from '@angular/core';
import {User,Theme} from './user';
@Component({
  selector: 'template-form',
  templateUrl: './template.driven.form.component.html',
  styleUrls: ['./template.driven.form.component.css']
})
export class TemplateDrivenFormComponent implements OnInit {

  
  private model: User = null;
  
  
  constructor() { }

  ngOnInit() {
    this.model={
      firstName:'',
      lastName:'',
      age:null,
      gender:this.genders[0].value,
      role:null,
      theme: this.themes[0],
      isActive:false ,
      toggle:this.toggles[1].value,
      topics:[this.topics[1].value],
      email:''
    };
  }
  onSubmit() {
    console.log('SUCCESS!! :-)\n\n' + JSON.stringify(this.model))
  }
  public save(isValid:boolean, f:User){
    console.log('If User is valid or not' + f + isValid);
    console.log('Json Array'+JSON.stringify(this.model));
  }


  public genders = [
    { value: 'F', display: 'Female' },
    { value: 'M', display: 'Male' }
];
public roles = [
    { value: 'admin', display: 'Administrator' },
    { value: 'guest', display: 'Guest' },
    { value: 'custom', display: 'Custom' }
];
public themes: Theme[] = [
    { backgroundColor: 'black', fontColor: 'white', display: 'Dark' },
    { backgroundColor: 'white', fontColor: 'black', display: 'Light' },
    { backgroundColor: 'grey', fontColor: 'white', display: 'Sleek' }
];
public topics = [
    { value: 'game', display: 'Gaming' },
    { value: 'tech', display: 'Technology' },
    { value: 'life', display: 'Lifestyle' },
];
public toggles = [
    { value: 'toggled', display: 'Toggled' },
    { value: 'untoggled', display: 'UnToggled' },
];
}
