import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FileUploadRoutingModule } from './file-upload-routing.module';
import {MatProgressBarModule} from '@angular/material/progress-bar';
import {MatDialogModule} from '@angular/material/dialog';
import {MatListModule} from '@angular/material/list';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {FileUploadComponent} from './file.upload.component';
import { DetailsUploadComponent } from './spring-file-upload/details-upload/details-upload.component';
import {FormUploadComponent  } from './spring-file-upload/form-upload/form-upload.component';
import {ListUploadComponent} from './spring-file-upload/list-upload/list-upload.component';
import {SpringFileUploadComponent} from './spring-file-upload/spring.file.upload.component';
import {UploadFileService} from './spring-file-upload/upload-file.service';
@NgModule({
  declarations: [
    FileUploadComponent,
    DetailsUploadComponent,
    FormUploadComponent,
    ListUploadComponent,
    SpringFileUploadComponent
  ],
  imports: [
    CommonModule,
    FileUploadRoutingModule,
    MatProgressBarModule,
    MatDialogModule,
    MatListModule,
    BrowserAnimationsModule

  ],
  providers:[UploadFileService],
  exports:[
    FileUploadComponent
  ],
  entryComponents:[]
})
export class FileUploadModule { }
