

import { NgModule, Component, OnDestroy, OnInit } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { platformBrowserDynamic } from "@angular/platform-browser-dynamic";
import { interval, Subscription } from "rxjs";
import { take, map } from "rxjs/operators";
import {Observable} from 'rxjs';

@Component({
  selector: "async-pipe",
  templateUrl:'./async-pipe.component.html',
  styleUrls: ['./async-pipe.component.scss']
})
export class AsyncPipeComponent implements OnDestroy,OnInit {
  
  time:Date;
  timeSub :Subscription; 
  todaysDate = new Date();
  jsonVal :{ moo: 'foo', goo: { too: 'new' }};
  constructor() {
    
  }
  ngOnInit(): void {
    this.timeSub = interval(1000).pipe(map(val => new Date())).subscribe(val =>this.time=val);  
    }
  
    time$ = interval(1000).pipe(map(val => new Date()));
  
  // AsyncPipe unsubscribes from the observable automatically
  ngOnDestroy() {
   
    if(this.timeSub){
      this.timeSub.unsubscribe();
    }
  }
}




