import { Component, OnInit } from '@angular/core';
import {Chart} from 'chart.js';

@Component({
  selector: 'app-charts',
  templateUrl: './charts.component.html',
  styleUrls: ['./charts.component.css']
})
export class ChartsComponent implements OnInit {

  BarChart=[];
  LineChart=[];
  RadarChart=[];
  DoughnutChart=[];
  PieChart=[];
  PolarAreaChart=[];
  BubbleChart=[];
  ScatterChart=[];
  //charts =[];
  constructor() { }

  /*
   line,
    radar,
    doughnut and pie,
    polar area,
    bubble,
    scatter
  */
  ngOnInit() {

    this.LineChart = new Chart('lineChart',this.getChartData('line') );
    this.BarChart = new Chart('barChart',this.getChartData('bar') );
    this.RadarChart = new Chart('radarChart',this.getChartData('radar') );
    this.DoughnutChart = new Chart('doughNutChart',this.getChartData('doughnut') );
    this.PieChart = new Chart('pieChart',this.getChartData('pie') );
    this.PolarAreaChart = new Chart('polarAreaChart',this.getChartData('polarArea') );
    this.BubbleChart = new Chart('bubbleChart',this.getChartData('bubble') );
    this.ScatterChart = new Chart('scatterChart',this.getChartData('scatter') );

  }

  getChartData(type:string){
    var chartTemp = {
      type: type,
      data: {
          labels: ["Red", "Blue", "Yellow", "Green", "Purple", "Orange"],
          datasets: [{
              label: '# of Votes',
              data: [12, 19, 3, 5, 2, 3],
              backgroundColor: [
                  'rgba(255, 99, 132, 0.2)',
                  'rgba(54, 162, 235, 0.2)',
                  'rgba(255, 206, 86, 0.2)',
                  'rgba(75, 192, 192, 0.2)',
                  'rgba(153, 102, 255, 0.2)',
                  'rgba(255, 159, 64, 0.2)'
              ],
              borderColor: [
                  'rgba(255,99,132,1)',
                  'rgba(54, 162, 235, 1)',
                  'rgba(255, 206, 86, 1)',
                  'rgba(75, 192, 192, 1)',
                  'rgba(153, 102, 255, 1)',
                  'rgba(255, 159, 64, 1)'
              ],
              borderWidth: 1
          }]
      },
      options: {
          scales: {
              yAxes: [{
                  ticks: {
                      beginAtZero:true
                  }
              }]
          }
      }
  }
    return  chartTemp ;
  }
  }
