import { Component } from "@angular/core";

@Component({
    selector:'app-vote-taker',
    template:`
        <h2>Parent listens for child event</h2>
        <h2>Should we colonize universe:</h2>
        <h3>Agreed : {{agreed}} , Diagree :{{disAgreed}}</h3>
        <app-voter *ngFor="let vote of voters" [name]="vote" (voted)="onVoted($event)" ></app-voter>
    `
})
export class VoteTakerComponent{

    agreed=0;
    disAgreed=0;
    voters=["Alphons","Victor","Duke"];

    onVoted(agreed:boolean){
        agreed ? this.agreed++ : this.disAgreed++;
    }
}