import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable,of } from 'rxjs';
import { Book } from './book';
import { LOCAL_STORAGE, SESSION_STORAGE, WebStorageService } from 'angular-webstorage-service';

@Injectable({
    providedIn: 'root'
})
export class BookService {
    //bookUrl='/api/books';
    bookUrl = 'http://localhost:3000/books';
   
    users: any[] = JSON.parse(localStorage.getItem('users')) || [];
    public data: Book []=[
        { "id": 1, "name": "Maths", "category": "Generic" },
        { "id": 2, "name": "Physics", "category": "Science" },
        { "id": 3, "name": "Chemistry", "category": "Science" },
        { "id": 4, "name": "Biology", "category": "Science" },
        { "id":51, "name": "History", "category": "Social" }
  
  ];
    constructor(private http: HttpClient, @Inject(LOCAL_STORAGE) private storage: WebStorageService) { }

    getBooksFromStore(): Observable<Book[]> {
      //  return this.http.get<Book[]>(this.bookUrl);
      return of(this.data);
    }

    getFavBookFromStore(id: number): Observable<Book> {
       // return this.http.get<Book>(this.bookUrl + '/' + id);
       return of(this.data[id]);
    }

    getBooksByCategoryFromStore(category: string): Observable<Book[]> {
        return this.http.get<Book[]>(this.bookUrl + '?category=' + category);
    }

    
    saveInLocal(key, val): void {
        console.log('recieved= key:' + key + 'value:' + val);
        this.storage.set(key, val);
        this.data[key] = this.storage.get(key);
    }

    getFromLocal(key): Book {
        console.log('recieved= key:' + key);
        this.data[key] = this.storage.get(key);
        console.log(this.data);
        return this.data[key];
    }

    tempBook :  Book ;
    public storeOnLocalStorage(book: Book): void {
        this.tempBook.id = book.id;
        this.tempBook.name = book.name;
        this.tempBook.category = book.category;
        this.data.push(this.tempBook);
        };

    public createBookList():void{
        for(let b of this.data){
            this.storage.set(''+b.id,b);
        }

    }
} 