import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DialogComponent } from './dialog/dialog.component'
import { MiscComponent } from './misc.component';
import { ShadowComponent,HighlightComponent,ChangeTextComponent } from './folder';
import {DragDropComponent} from './drag-drop/drag-drop.component';
import {VirtualScrollComponent} from './virtual-scroll/virtual-scroll.component';
import {ViewContentComponent} from './view-content/view-content.component';
import {AsyncPipeComponent} from './async-pipe/async-pipe.component';
import {LifecycleHooksComponent} from './lifecycle-hooks/lifecycle-hooks.component';
import {NgTempCompComponent} from './ng-temp-comp/ng-temp-comp.component';
import {InputOutputComponent} from './input-output/input-output.component';
import { PageNotFoundComponent } from '../page-not-found/page-not-found.component';
const routes: Routes = [
  {
    path: 'misc',
    component: MiscComponent,
    children: [
      {
        path: 'dialog',
        component: DialogComponent
      },
      {
        path: 'shadow',
        component: ShadowComponent,
        outlet:'shadowOutlet'
      },
      {
        path: 'highlight',
        component: HighlightComponent,
        outlet:'highlightOutlet'
      },
      {
        path:'changeText',
        component:ChangeTextComponent,
        outlet:'changeTextOutlet'
      },
      {
        path:'drag-drop',
        component:DragDropComponent,
        outlet:'dragDropOutlet'
      },
      {
        path:'virtual-scroll',
        component:VirtualScrollComponent,
        outlet:'virtualScrollOutlet'
      },
      {
        path:'view-content',
        component:ViewContentComponent,
        outlet:'viewContentOutlet'
      },
      {
        path:'pipe-demo',
        component:AsyncPipeComponent,
        outlet:'pipeDemoOutlet'
      },
      {
        path:'life-cycle',
        component:LifecycleHooksComponent,
        outlet:'lifeCycleOutlet'
      },
      {
        path:'template-container',
        component:NgTempCompComponent,
        outlet:'templateContainerOutlet'
      },
	  {
		path:'input-output',
    component:InputOutputComponent,
    outlet:'inputOutputOutlet'
    },
    {path: '**',component: PageNotFoundComponent }	
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MiscRoutingModule { }
