import {  
    ReactiveFormsModule,  
    NG_VALIDATORS,  
    FormsModule,  
    FormGroup,  
    FormControl,  
    ValidatorFn,  
    Validator  
   } from '@angular/forms';  
   import { Directive } from '@angular/core';  
   @Directive({  
    selector: '[agevalidator][ngModel]',  
    providers: [  
     {  
      provide: NG_VALIDATORS,  
      useExisting: AgeValidator,  
      multi: true  
     }  
    ]  
   })  
   export class AgeValidator implements Validator {  
    validator: ValidatorFn;  
    constructor() {  
     this.validator = this.ageValidator();  
    }  
    validate(c: FormControl) {  
     return this.validator(c);  
    }  
   
   ageValidator(): ValidatorFn {  
     return (c: FormControl) => {  
    
    let isValid = false;
  
    if (null != c.value && c.value > 0 && c.value < 100) {
      isValid = true;
    }
//      let isValid = /^[_a-z0-9]+(\.[_a-z0-9]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,4})$/.test(c.value);  
      if (isValid) {  
       return null;  
      } else {  
       return {  
        agevalidator: {  
         valid: false  
        }  
       };  
      }  
     }  
    }  
   }