import { Component, OnInit, AfterViewInit, Inject, ElementRef } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import * as Popper from 'popper.js/dist/umd/popper.js'
import { DOCUMENT } from '@angular/common';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  userName:string ="";
  dateMessage : string ;
  response: any ;


  constructor(private http: HttpClient,@Inject(DOCUMENT) private document, private elementRef: ElementRef) { 
    setInterval(()=>{
      let currentDate = new Date();
      this.dateMessage = currentDate.toDateString() +' '+currentDate.toLocaleTimeString()  ;

    },1000);
  }

  ngOnInit() {
  
  }
search(){
 this.http.get('https://api.github.com/users/'+ this.userName)
 .subscribe((response)=>{
   this.response = response;
   console.log(this.response);
 });

}
}
